/** @format */

@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,100&display=swap");

* {
	margin: 0;
	padding: 0;
	font-family: "Lato", sans-serif;
}

.btn {
	background: #353b48;
	height: 190%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.btn {
	width: 200px;
	height: 60px;
	background: none;
	border: 4px solid;
	color: #3498db;
	font-weight: 700;
	text-transform: uppercase;
	cursor: pointer;
	font-size: 16px;
}

.btn::before,
.btn::after {
	content: "";
	position: absolute;
	width: 14px;
	height: 4px;
	/* background: #353b48; */
	transform: skewX(50deg);
	transition: 4s linear;
}

.btn::before {
	top: -4px;
	left: 10%;
}

.btn::after {
	bottom: -4px;
	right: 10%;
}

.btn:hover::before {
	left: 80%;
}

.btn:hover::after {
	right: 80%;
}
