/* styling for scroll bar */
::-webkit-scrollbar {
	width: 5px;
	height: 6px;
}

::-webkit-scrollbar-track {
	box-shadow: inset 0 0 5px #a5aaad;
	border-radius: 10px;
}

::-webkit-scrollbar-thumb {
	background: #3ea175;
	border-radius: 10px;
}

::-webkit-scrollbar-track:hover {
	background: #a5aaad;
}

* {
	margin: 0;
	padding: 0;
}

body {
	font-family: "Lato", sans-serif;
	box-sizing: border-box;
}
.text-primary-p {
	color: #a5aaad;
	font-size: 14px;
	font-weight: 700;
}

.font-bold {
	font-weight: 700;
}

.text-title {
	color: #2e4a66;
}

.text-ligthblue {
	color: #469cac;
}

.text-red {
	color: #cc3d38;
}

.text-yellow {
	color: #a98921;
}

.text-green {
	color: #3b9668;
}

.container {
	display: grid;
	height: 100vh;
	grid-template-columns: 0.8fr 1fr 1fr 1fr;
	grid-template-rows: 0.2fr 3fr;
	grid-template-areas: "sidebar nav nav nav" "sidebar main main main";
}

.navbar {
	background: #ffffff;
	grid-area: nav;
	height: 70px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 30px 0 30px;
	border-bottom: 1px solid lightgray;
}

.nav__icon {
	display: none;
}

.nav__icon > i {
	font-size: 26px;
	color: #a5aaad;
}

.navbar__left > a {
	margin-right: 30px;
	text-decoration: none;
	color: #a5aaad;
	font-size: 15px;
	font-weight: 700;
}

.navbar__left .active_link {
	color: #265acc;
	border-bottom: 3px solid #265acc;
	padding-bottom: 12px;
}

.navbar__right {
	display: flex;
	justify-content: center;
	align-items: center;
}

.navbar__right > a {
	margin-left: 20px;
	text-decoration: none;
}

.navbar__right > a > i {
	color: #a5aaad;
	font-size: 16px;
	border-radius: 50px;
	background: #ffffff;
	box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #ffffff;
}

main {
	background: #f3f4f6;
	grid-area: main;
	overflow-y: auto;
}

.main_conatiner {
	padding: 20px 50px;
}

.main_title {
	display: flex;
	align-items: center;
}

.main_title > img {
	max-height: 100px;
	object-fit: contain;
	margin-right: 20px;
}

.main_greeting > h1 {
	font-size: 24px;
	color: #2e4a66;
	margin-bottom: 5px;
}

.main_greeting > p {
	font-size: 14px;
	font-weight: 700;
	color: #a5aaad;
}

.main_cards {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-gap: 30px;
	gap: 30px;
	margin: 20px 0;
}

.card {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100px;
	padding: 25px;
	background: #ffffff;
	box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
}

.card_inner {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.card_inner > span {
	font-size: 25px;
}

.charts {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 30px;
	gap: 30px;
	margin-top: 50px;
}

.charts_left {
	padding: 25px;
	border-radius: 5px;
	background: #ffffff;
	box-shadow: 5px 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
}

.schedule {
	padding: 25px;
	border-radius: 5px;
	width: 207%;
	background: #a2eee0;
	box-shadow: 5px 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
}

.charts_left_title {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.schedule_title {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.charts_left_title > div > h1 {
	font-size: 24px;
	color: #2e4a66;
	margin-bottom: 5px;
}

.schedule_title > div > h1 {
	font-size: 24px;
	color: #2e4a66;
	margin-bottom: 5px;
}

.charts_left_title > div > p {
	font-size: 14px;
	font-weight: 700;
	color: #a5aaad;
	margin-bottom: 10px;
}

.schedule_title > div > p {
	font-size: 14px;
	font-weight: 700;
	color: #1f2021;
	margin-bottom: 10px;
}

.charts_left_title > i {
	color: #ffffff;
	font-size: 20px;
	background: #ffc100;
	border-radius: 200px 0px 200px 200px;
	-moz-border-radius: 200px 0px 200px 200px;
	-webkit-border-radius: 200px 0px 200px 200px;
	border: 0px solid #000000;
	padding: 15px;
}

.schedule_title > i {
	color: #ffffff;
	font-size: 20px;
	background: #ffc100;
	border-radius: 200px 0px 200px 200px;
	-moz-border-radius: 200px 0px 200px 200px;
	-webkit-border-radius: 200px 0px 200px 200px;
	border: 0px solid #000000;
	padding: 15px;
}

.charts_rigth {
	padding: 25px;
	border-radius: 5px;
	background: #ffffff;
	box-shadow: 5px 5px 5px 13px #ededed;
	box-shadow: 5px 5px 5px 13px #ededed, -5px -5px 13px #ffffff;
}

.charts_rigth_title {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.charts_rigth_title > div > h1 {
	font-size: 24px;
	color: #2e4a66;
	margin-bottom: 5px;
}

.charts_rigth_title > div > p {
	font-size: 14px;
	font-weight: 700;
	color: #a5aaad;
	margin-bottom: 10px;
}

.charts_rigth_title > i {
	color: #ffffff;
	font-size: 20px;
	background: #39447a;
	border-radius: 200px 0px 200px 200px;
	-moz-border-radius: 200px 0px 200px 200px;
	-webkit-border-radius: 200px 0px 200px 200px;
	border: 0px solid #000000;
	padding: 15px;
}

.charts_rigth_cards {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 20px;
	gap: 20px;
	nargin-top: 30px;
}

.table-box {
	margin: 50px auto;
}

.table-row {
	display: table;
	width: 80%;
	margin: 10px auto;
	font-family: "Lato" sans-serif;
	background: transparent;
	padding: 12px;
	color: #555;
	font-size: 13px;
	box-shadow: 0 1px 4px 0 rgba(0, 0, 50, 0.3);
}

.table-cell {
	display: table-cell;
	width: 30%;
	text-align: center;
	padding: 4px 0;
	border-right: 1px solid #fc2727;
	vertical-align: middle;
}

.table-head {
	background: #c4fc75;
	box-shadow: none;
	color: rgb(21, 20, 20);
	font-weight: 750;
}

.table-head .table-cell {
	border-right: none;
}

.last-cell {
	border-right: none;
}

.first-cell {
	text-align: left;
	padding-left: 10px;
}

.card1 {
	background: #d1ecf1;
	color: #35a4ba;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}

.card2 {
	background: #d2f9ee;
	color: #38e1b0;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}

.card3 {
	background: #d6d8d9;
	color: #3a3e41;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}
.card4 {
	background: #fddcdf;
	color: #f65a6f;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}

#sidebar {
	background: #020509;
	grid-area: sidebar;
	overflow-y: auto;
	padding: 20px;
	transition: all 0.5s;
}

.sidebar_img {
	display: flex;
	align-items: center;
	color: #3498db;
}

.sidebar_title > div > img {
	width: 75px;
	object-fit: contain;
}

.sidebar_title > div > h1 {
	font-size: 18px;
	display: inline;
}

.sidebar_title > i {
	font-size: 18px;
	display: none;
}

.sidebar_title {
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: #f3f4f6;
	margin-bottom: 30px;
}

.sidebar_menu > h2 {
	color: #3ea175;
	font-size: 16px;
	margin-top: 15px;
	margin-bottom: 5px;
	padding: 0 10px;
	font-weight: 700;
}

.sidebar_link {
	color: #f3f4f6;
	padding: 10px;
	border-radius: 3px;
	margin-bottom: 5px;
}

.active_menu_link {
	background: rgba(62, 161, 117, 0.3);
	color: #3ea175;
}

.active_menu_link a {
	color: #3ea175 !important;
}

.sidebar_link > Link {
	text-decoration: none;
	color: #a5aaad;
	font-weight: 700;
}

.sidebar_link > a {
	text-decoration: none;
	color: #a5aaad;
	font-weight: 700;
}

.sidebar_link > i {
	margin-right: 10px;
	font-size: 18px;
}

.sidebar_logout {
	margin-top: 20px;
	padding: 10px;
	color: #e65061;
}

.sidebar_logout > a {
	text-decoration: none;
	color: #e65061;
	font-weight: 700;
	text-transform: uppercase;
}

.sidebar_logout > i {
	margin-right: 10px;
	font-size: 18px;
}

.sidebar_responsive {
	display: inline !important;
	z-index: 9999 !important;
	left: 0 !important;
	position: absolute;
}

/* desktop */

@media only screen and (max-width: 978px) {
	.container {
		grid-template-columns: 1fr;
		grid-template-rows: 0.2fr 3fr;
		grid-template-areas: "nav" "main";
	}

	#sidebar {
		display: none;
	}

	.sidebar_title > i {
		display: inline;
	}

	.nav__icon {
		display: inline;
	}
}

/* tablet */

@media only screen and (max-width: 855px) {
	.main_cards {
		grid-template-columns: 1fr;
		grid-gap: 10px;
		gap: 10px;
		margin-bottom: 0;
	}
	.charts {
		grid-template-columns: 1fr;
		margin-top: 30px;
	}
}

/* phone */
@media only screen and (max-width: 480px) {
	.navbar__left {
		display: none;
	}
}

/** @format */

* {
	margin: 0;
	padding: 0;
	font-family: "Lato", sans-serif;
}

.btn {
	background: #353b48;
	height: 190%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.btn {
	width: 200px;
	height: 60px;
	background: none;
	border: 4px solid;
	color: #3498db;
	font-weight: 700;
	text-transform: uppercase;
	cursor: pointer;
	font-size: 16px;
}

.btn::before,
.btn::after {
	content: "";
	position: absolute;
	width: 14px;
	height: 4px;
	/* background: #353b48; */
	transform: skewX(50deg);
	transition: 4s linear;
}

.btn::before {
	top: -4px;
	left: 10%;
}

.btn::after {
	bottom: -4px;
	right: 10%;
}

.btn:hover::before {
	left: 80%;
}

.btn:hover::after {
	right: 80%;
}

